import { BpmnModel } from "../BpmnModel";
import { BpmnModelElement } from "../BpmnModelElement";
import { BpmnModelHelper } from "../BpmnModelHelper";
import { IHasIdElement } from "../IHasIdElement";

export abstract class OmgdiElement extends BpmnModelElement implements IHasIdElement {
    constructor(model: BpmnModel) {
        super(model);

        this.Id = BpmnModelHelper.GenerateElementId();
    }

    Id: string;

    protected ResetBeforeLoadFrom(): void {
        super.ResetBeforeLoadFrom();

        this.Id = null;
    }

    ConvertToJsonObject(): any {
        let obj = super.ConvertToJsonObject();

        BpmnModelHelper.SetAttributeValue(obj, "Id", this.Id);

        return obj;
    }

    LoadFromJsonObject(obj: any): void {
        super.LoadFromJsonObject(obj);

        this.Id = obj.Id;
    }
}
